.tangible-slider {
  opacity: 0;
  transition: opacity 0.3s;
}

.tangible-slider.loaded {
  opacity: 1;
}

.tslide-outer {
  overflow: hidden;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tslider:before,
.tslider:after {
  content: " ";
  display: table;
}

.tslider {
  overflow: hidden;
  margin: 0;
}

.tslide-wrapper {
  max-width: 100%;
  overflow: hidden;
  position: relative;
}

.tslide-wrapper > .tslider:after {
  clear: both;
}

.tslide-wrapper .tslide-animate {
  -webkit-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
  -webkit-transition: all 1s;
  -webkit-transition-property: -webkit-transform, height;
  -moz-transition-property: -moz-transform, height;
  transition-property: transform, height;
  -webkit-transition-duration: inherit !important;
  transition-duration: inherit !important;
  -webkit-transition-timing-function: inherit !important;
  transition-timing-function: inherit !important;
}

.tslide-wrapper .tslider-fade {
  position: relative;
}

.tslide-wrapper .tslider-fade > * {
  position: absolute !important;
  top: 0;
  left: 0;
  z-index: 9;
  margin-right: 0;
  width: 100%;
}

/* May not work with carousel mode */
.tslide-wrapper .tslider-fade img {
  width: 100%;
}

.tslide-wrapper.usingCss .tslider-fade > * {
  opacity: 0;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  -webkit-transition-duration: inherit !important;
  transition-duration: inherit !important;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-timing-function: inherit !important;
  transition-timing-function: inherit !important;
}

.tslide-wrapper .tslider-fade > *.active {
  z-index: 10;
}

.tslide-wrapper.usingCss .tslider-fade > *.active {
  opacity: 1;
}

/* slider actions */
.tslider-action > a {
  width: 32px;
  display: block;
  top: 50%;
  height: 32px;
  background-image: url(data:image/png;base64,);
  cursor: pointer;
  position: absolute;
  z-index: 99;
  margin-top: -16px;
  opacity: 0.5;
  -webkit-transition: opacity 0.35s linear 0s;
  transition: opacity 0.35s linear 0s;
}

.tslider-action > a:hover {
  opacity: 1;
}

.tslider-action > .tslider-prev {
  background-position: 0 0;
  left: 10px;
}

.tslider-action > .tslider-next {
  background-position: -32px 0;
  right: 10px;
}

.tslider-action > a.disabled {
  pointer-events: none;
}

.cS-hidden {
  height: 1px;
  opacity: 0;
  filter: alpha(opacity=0);
  overflow: hidden;
}

/* vertical */
.tslide-outer.vertical {
  position: relative;
}

.tslide-outer.vertical.noPager {
  padding-right: 0px !important;
}

.tslide-outer.vertical .tslider-gallery {
  position: absolute !important;
  right: 0;
  top: 0;
}

.tslide-outer.vertical .tslider > * {
  width: 100% !important;
  max-width: none !important;
}

/* vertical controlls */
.tslide-outer.vertical .tslider-action > a {
  left: 50%;
  margin-left: -14px;
  margin-top: 0;
}

.tslide-outer.vertical .tslider-action > .tslider-next {
  background-position: 31px -31px;
  bottom: 10px;
  top: auto;
}

.tslide-outer.vertical .tslider-action > .tslider-prev {
  background-position: 0 -31px;
  bottom: auto;
  top: 10px;
}

/* vertical */
/* Rtl */
.tslide-outer.tslider-rtl {
  direction: rtl;
}

.tslide-outer .tslider,
.tslide-outer .tslider-pager {
  padding-left: 0;
  list-style: none outside none;
}

.tslide-outer.tslider-rtl .tslider,
.tslide-outer.tslider-rtl .tslider-pager {
  padding-right: 0;
}

.tslide-outer .tslider > *,
.tslide-outer .tslider-gallery li {
  float: left;
}

.tslide-outer.tslider-rtl .tslider > *,
.tslide-outer.tslider-rtl .tslider-gallery li {
  float: right !important;
}

/* Rtl */
@-webkit-keyframes rightEnd {
  0% {
    left: 0;
  }
  50% {
    left: -15px;
  }
  100% {
    left: 0;
  }
}
@keyframes rightEnd {
  0% {
    left: 0;
  }
  50% {
    left: -15px;
  }
  100% {
    left: 0;
  }
}
@-webkit-keyframes topEnd {
  0% {
    top: 0;
  }
  50% {
    top: -15px;
  }
  100% {
    top: 0;
  }
}
@keyframes topEnd {
  0% {
    top: 0;
  }
  50% {
    top: -15px;
  }
  100% {
    top: 0;
  }
}
@-webkit-keyframes leftEnd {
  0% {
    left: 0;
  }
  50% {
    left: 15px;
  }
  100% {
    left: 0;
  }
}
@keyframes leftEnd {
  0% {
    left: 0;
  }
  50% {
    left: 15px;
  }
  100% {
    left: 0;
  }
}
@-webkit-keyframes bottomEnd {
  0% {
    bottom: 0;
  }
  50% {
    bottom: -15px;
  }
  100% {
    bottom: 0;
  }
}
@keyframes bottomEnd {
  0% {
    bottom: 0;
  }
  50% {
    bottom: -15px;
  }
  100% {
    bottom: 0;
  }
}
.tslide-outer .rightEnd {
  -webkit-animation: rightEnd 0.3s;
  animation: rightEnd 0.3s;
  position: relative;
}

.tslide-outer .leftEnd {
  -webkit-animation: leftEnd 0.3s;
  animation: leftEnd 0.3s;
  position: relative;
}

.tslide-outer.vertical .rightEnd {
  -webkit-animation: topEnd 0.3s;
  animation: topEnd 0.3s;
  position: relative;
}

.tslide-outer.vertical .leftEnd {
  -webkit-animation: bottomEnd 0.3s;
  animation: bottomEnd 0.3s;
  position: relative;
}

.tslide-outer.tslider-rtl .rightEnd {
  -webkit-animation: leftEnd 0.3s;
  animation: leftEnd 0.3s;
  position: relative;
}

.tslide-outer.tslider-rtl .leftEnd {
  -webkit-animation: rightEnd 0.3s;
  animation: rightEnd 0.3s;
  position: relative;
}

/*/  Grab cursor */
.tslider.tslider-grab > * {
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: -o-grab;
  cursor: -ms-grab;
  cursor: grab;
}

.tslider.tslider-grabbing > * {
  cursor: move;
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: -o-grabbing;
  cursor: -ms-grabbing;
  cursor: grabbing;
}

.tslide-outer .tslider-pager.tslider-gallery {
  list-style: none outside none;
  padding-left: 0;
  margin: 0;
  overflow: hidden;
  transform: translate3d(0px, 0px, 0px);
  -moz-transform: translate3d(0px, 0px, 0px);
  -ms-transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
  -o-transform: translate3d(0px, 0px, 0px);
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tslide-outer .tslider-pager.tslider-gallery li {
  overflow: hidden;
  -webkit-transition: border-radius 0.12s linear 0s 0.35s linear 0s;
  transition: border-radius 0.12s linear 0s 0.35s linear 0s;
}

.tslide-outer .tslider-pager.tslider-gallery li.active,
.tslide-outer .tslider-pager.tslider-gallery li:hover {
  border-radius: 5px;
}

.tslide-outer .tslider-pager.tslider-gallery img {
  display: block;
  height: auto;
  max-width: 100%;
}

.tslide-outer .tslider-pager.tslider-gallery:before,
.tslide-outer .tslider-pager.tslider-gallery:after {
  content: " ";
  display: table;
}

.tslide-outer .tslider-pager.tslider-gallery:after {
  clear: both;
}

.tslide-outer .tslider-pager.tslider-pager {
  margin: 10px 0 0;
  padding: 0;
  text-align: center;
}

.tslide-outer .tslider-pager.tslider-pager > li {
  cursor: pointer;
  display: inline-block;
  padding: 0 5px;
}

.tslide-outer .tslider-pager.tslider-pager > li a {
  background-color: #222222;
  border-radius: 30px;
  display: inline-block;
  height: 8px;
  overflow: hidden;
  text-indent: -999em;
  width: 8px;
  position: relative;
  z-index: 99;
  -webkit-transition: all 0.5s linear 0s;
  transition: all 0.5s linear 0s;
}

.tslide-outer .tslider-pager.tslider-pager > li:hover a,
.tslide-outer .tslider-pager.tslider-pager > li.active a {
  background-color: #428bca;
}

.tslide-outer .media {
  opacity: 0.8;
}

.tslide-outer .media.active {
  opacity: 1;
}